<template>
  <div class="template-container" v-loading="loading">
    <div class="template-container-title">新增标的物</div>

    <el-form ref="form" :model="form">
      <!--标的物类型 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">标的物类型</div>
        <div class="temp-item-main">
          <el-radio-group v-model="form.product_type"  @change="productTypeChange" class="temp-radio-group">
            <div class="temp-radio-item" v-for="(item, key) in PRODUCT_REFS_TYPE_list">
              <el-radio :label="key">{{item.name}}</el-radio>
              <span class="temp-radio-remark">{{item.desc}}</span>
            </div>
          </el-radio-group>
        </div>
      </div>
      <!--标的物类型 end -->
      <!--标的物设置 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">标的物设置</div>
        <div class="temp-item-main">
          <div class="temp-item-block" v-show='productNameShow'>
            <label class="temp-item-label" for="">标的物名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-item-inputtext" v-model="form.product_name" type="text" value="" />
            </div>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" style="padding-top:7px;" for="">标的物id：</label>
            <div class="temp-item-inlineblock">
              <!-- <input class="temp-item-inputtext" v-model="form.product_refs_id" type="text" value="" /> -->
              <el-autocomplete popper-class="my-autocomplete" v-model="form.product_refs_id"
                :fetch-suggestions="querySearch"
                placeholder="请输入标的物id" @select="handleSelect">
                <i
                  class="el-icon-edit el-input__icon"
                  slot="suffix"
                  @click="handleIconClick">
                </i>
                <template slot-scope="props" >
                  <div class="name" >{{ props.item.app_name }}</div>
                </template>
              </el-autocomplete>

            </div>
          </div>
        </div>
      </div>
      <!--计划设置 end -->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    this.getEnumList()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  mounted() {
    this.loadAll();
  },
  data() {
    return {
      apps: [],
      appList: [],
      productNameShow: true,
      loading: false,
      form: {
        product_type: '',
        product_name: '',
        product_refs_id: ''
      },
      PRODUCT_REFS_TYPE_list: {}
    }
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/product')
    },
    onSubmit() {
      var params = this.form
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Products/add', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/setting/product')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    getEnumList() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.PRODUCT_REFS_TYPE_list = respData.data['PRODUCT_REFS_TYPE']
            console.log(that.PRODUCT_REFS_TYPE_list)
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    productTypeChange(value){
      if(value == 'PRODUCT_TYPE_APP_ANDROID_OPEN_PLATFORM' || value == 'PRODUCT_TYPE_APP_IOS'){
        this.productNameShow = false
        if(value == 'PRODUCT_TYPE_APP_IOS'){
          this.apps = this.appList.filter(this.createFilterPlatform('ios'))
        }else{
          this.apps = this.appList.filter(this.createFilterPlatform('android'))
        }
      }else{
        this.productNameShow = true
        this.apps = []
      }
    },
    querySearch(queryString, cb) {
      var apps = this.apps;
      var results = queryString ? apps.filter(this.createFilter(queryString)) : apps;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.appstore_id.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    createFilterPlatform(platform) {
      return (restaurant) => {
        return (restaurant.platform == platform);
      };
    },
    loadAll() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Products/getApp', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.appList = respData.data
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    handleSelect(item) {
      this.form.product_refs_id = item.appstore_id
    },
    handleIconClick(ev) {
      console.log(ev);
    }
  },
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}
</style>



